.LineIndicators {
    box-shadow: inset 0 -1px 0 rgba(158, 161, 169, 0.15);
    height: 64px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

    .LineIndicator {
        height: 8px;
        width: 8px;
        margin: 0 5px;
        &--0 {
            background-color: #D8D8D8; }
        &--1, &--2 {
            background-color: #ECD81D; }
        &--7, &--8 {
            background-color: #E34D25; }
        &--9, &--10, &--11, &--12 {
            background-color: #2591E3; } } }

